.testDev {
  display: flex;
}

.testwrap {
  margin-top: 15em;
  display: flex;
}

.sets {
  display: flex;
  margin-left: 4em;
}

#colorboxInit, #colorboxProcessed, #harmonicComplementary, #harmonicAnalogous {
  --size: 100px;
  height: var(--size);
  width: var(--size);
}

.clr-field > button {
  display: none;
}

.colInputs {
  width: 60px;
  height: 60px;
  color: transparent;
  text-shadow: none;
  user-select: none;
  border-radius: 5px;
}

.tc {
  --size: 40px;
  height: var(--size);
  width: var(--size);
  background: red;
}